import 'rxjs/add/operator/timeoutWith';
@Injectable({
  providedIn: 'root'
})
export class CrudService {

  constructor(private http:HttpClient) {}

    getuser(){
return this.http.get('http://localhost:3000/user').timeoutWith(18, Observable.throw(new Error('Request got timed out')));
    }
  
   }

